#goTop {
    position: fixed;
    z-index: 4;
    right: 30px;
    bottom: -100px;
    width: 58px;
    height: 58px;
    transition: all 0.6s ease-in-out;
    &.on {
        bottom: 20px;
    }
    a {
        display: block;
        width: 100%;
        height: 100%;
        img {
            width: 100%;
        }
    }
}