#sub7View {
    width: 100%;
    #section1 {
        width: 100%;
        padding: 50px 0 100px 0;
        .container {
            width: 100%;
            max-width: 1050px;
            margin: 0 auto;
            .title {
                width: 100%;
                text-align: center;
                h2 {
                    font-size: 28px;
                    color: #333;
                    font-weight: 600;
                }
                h3 {
                    font-size: 14px;
                    color: #999;
                    padding: 10px 0 0 0;
                }
            }
            .content {
                width: 100%;
                padding: 70px 0 0 0;
                .view-box {
                    width: 100%;
                    border-top: 2px solid #333;
                    padding: 0 0 100px 0;
                    > ul{
                        width: 100%;
                        >li {
                            width: 100%;
                            ul {
                                width: 100%;
                                li {
                                    width: 100%;
                                    display: flex;
                                    border-bottom: 1px solid #f4f4f4;
                                    > div {
                                        height: 44px;
                                        display: flex;
                                        align-items: center;
                                        font-size: 14px;
                                        &.left {
                                            width: 150px;
                                            background: #f7f5f8;
                                            strong {
                                                color: #333;
                                                font-weight: 500;
                                                padding: 0 0 0 20px;
                                            }
                                        }
                                        &.right {
                                            width: 900px;
                                            p {
                                                padding: 0 0 0 20px;
                                                color: #666;
                                            }

                                        }
                                    }

                                }
                            }
                            .gap {
                                width: 100%;
                                padding: 20px;
                                .contents { // div 안에는 p태그가 들어갈 수 있지만 p태그안에 p태그는 들어갈 수 없음
                                    width: 100%;
                                    p {
                                        width: 80%;
                                        padding: 5px 0;
                                        font-size: 14px;
                                        line-height: 140%;
                                        color: #666;
                                        text-align: justify; // 글자 끝이 딱 맞음
                                    }
                                }   
                            }
                        }
                    }
                }
                .button-box {
                    width: 100%;
                    border-top: 1px solid #ddd;
                    padding: 20px 0 0 0;
                    text-align: center;
                    button {
                        width: 140px;
                        height: 52px;
                        background: #5f0080;
                        border: 1px solid #5f0080;
                        border-radius: 3px;
                        color: #fff;
                        font-size: 14px;
                        font-weight: 600;
                        cursor: pointer;
                        margin: 0 0 0 10px;
                        opacity: 1;
                        transition: opacity 0.3s ease-in-out;
                        &:hover {
                            opacity: 0.9;
                        }
                    }
                }
            }
        }
    }
}